#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  --bg-opacity: 1;
  background-color: #F3EFDA;
  background-color: rgba(243, 239, 218, var(--bg-opacity));
  font-family: Lato
}

.btn-focus:focus {
  outline: 2px solid transparent;
  outline-offset: 2px
}

.btn-focus:focus-visible {
  outline: 2px solid transparent;
  outline-offset: 2px;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.5);
  --border-opacity: 1;
  border-color: #625D50;
  border-color: rgba(98, 93, 80, var(--border-opacity))
}

.container{
  width: 100%;
}

@media (min-width: 640px){
  .container{
    max-width: 640px;
  }
}

@media (min-width: 768px){
  .container{
    max-width: 768px;
  }
}

@media (min-width: 1024px){
  .container{
    max-width: 1024px;
  }
}

@media (min-width: 1280px){
  .container{
    max-width: 1280px;
  }
}

.divide-y > :not(template) ~ :not(template){
  --divide-y-reverse: 0;
  border-top-width: calc(1px * calc(1 - var(--divide-y-reverse)));
  border-bottom-width: calc(1px * var(--divide-y-reverse));
}

.divide-theme-extra-light-gray > :not(template) ~ :not(template){
  --divide-opacity: 1;
  border-color: #CCC5B3;
  border-color: rgba(204, 197, 179, var(--divide-opacity));
}

